import * as React from 'react';
import CustomMaterialTable from 'src/components/common/CustomMaterialTable';
import { useHistory } from 'react-router-dom';
import MainContext from 'src/context/MainContext';

export default function Table() {
  let [data, setData] = React.useState<any>([]);
  const { toggleLoading } = React.useContext(MainContext);

  const clickHandler = (event: React.MouseEvent | undefined, rowData: any) => {
    // const {type} = event.view.getSelection()
    if (event) {
      const browserEvent: MouseEvent = event.nativeEvent;
      const window: Window | null = event.nativeEvent.view;
      const selection = window ? window.getSelection() : null;
      const selectionType = selection ? selection.type : '';
      console.log(selectionType);
      if (selectionType === 'Range') {
        return;
      }
      if ((browserEvent.metaKey || browserEvent.ctrlKey) && window) {
        window.open('/table');
        return;
      }

      history.push('/home');
    }
  };

  const history = useHistory();

  let dataFetcher = new Promise((resolve, reject) => {
    // We call resolve(...) when what we were doing asynchronously was successful, and reject(...) when it failed.
    // In this example, we use setTimeout(...) to simulate async code.
    // In reality, you will probably be using something like XHR or an HTML5 API.
    let dataset = [
      {
        id: 7999,
        name: 'Mehmet',
        surname: 'Baran',
        birthYear: 1987,
        birthCity: 'asdfadsfasdfadsfadsfadsfadsfadsfadsfasdfadsfasdfasdfadsfadsfadsf',
      },
      {
        id: 8000,
        name: 'Zerya BetÃ¼l',
        surname: 'Baran',
        birthYear: 2017,
        birthCity: 'asdfadsfaadsfasdfadssdfadsfadsfadsfadsfadsfadsfasdfadsfasdfasdfadsfadsfadsf',
      },
    ];

    setTimeout(function () {
      resolve(dataset); // Yay! Everything went well!
    }, 52000);
  });

  React.useEffect(() => {
    toggleLoading(true);
    dataFetcher.then((result) => {
      setData(result);
      toggleLoading(false);
    });
    return () => {
      toggleLoading(false);
    };
  }, []);

  const className = {
    headerStyle: {
      padding: '8px 8px 8px 0',
      width: '5%',
    },
    cellStyle: {
      padding: '8px 8px 8px 0',
      width: '5%',
    },
  };

  return (
    <CustomMaterialTable
      columns={[
        { title: 'name', field: 'name', ...className },
        { title: 'surname', field: 'surname', ...className },
        {
          title: 'birthCity',
          field: 'birthCity',
          ...className,
        },
        {
          title: 'birthYear',
          field: 'birthYear',
          type: 'numeric',
          ...className,
        },
      ]}
      data={data}
      title="Demo Title"
      options={{
        search: true,
        tableLayout: 'auto',
      }}
      onRowClick={clickHandler}
    />
  );
}
