import * as React from 'react';
import MainContext from 'src/context/MainContext';

export default function MainContainer({ children }: { children: React.ReactNode }) {
  const toggleLoading = (show: boolean) => {
    toggleSpinner((prevState) => {
      let newCount = prevState.loadingCount;
      if (show) {
        newCount = newCount + 1;
      } else {
        newCount = newCount > 0 ? newCount - 1 : 0;
      }
      return {
        ...prevState,
        loadingCount: newCount,
      };
    });
  };

  const loadingState = {
    loadingCount: 0,
    toggleLoading,
  };

  const [spinner, toggleSpinner] = React.useState(loadingState);

  return <MainContext.Provider value={spinner}>{children}</MainContext.Provider>;
}
