export default function RouteController() {
  /* 
    To redirect to the new website
    <Route path="/old-match">
      <Redirect to="/will-match" />
    </Route> 
  */
  return (
    <Switch>
      <Route path="/table" component={Table} />
      <Route path="*" component={WorkPage} />
    </Switch>
  );
}

