import React from 'react';
import {
  Typography,
  makeStyles,
  Paper,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
  IconButton,
  Collapse,
} from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import clsx from 'clsx';
import { red } from '@material-ui/core/colors';
import ArrowRightIcon from '@material-ui/icons/ArrowRight';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(0),
    border: 1,
    color: theme.palette.text.secondary,
  },
  list: {
    padding: theme.spacing(0),
  },
  card: {
    backgroundColor: theme.palette.background.paper,
  },
  cardHeader: {
    paddingBottom: theme.spacing(1),
    paddingTop: theme.spacing(1),
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
  },
}));

interface IDuration {
  startDate: Date;
  endDate: Date | 'Present';
  asText: () => string;
}

interface IJobContent {
  header: string;
  duration: IDuration;
  title: string;
  descriptions: string[];
}

class Duration implements IDuration {
  startDate: Date;
  endDate: Date | 'Present';
  constructor(startDate: Date, endDate?: Date) {
    this.startDate = startDate;
    this.endDate = endDate || 'Present';
  }
  asText() {
    const month_names = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];

    function getDateString(date: Date) {
      return `${month_names[date.getMonth()]}/${date.getFullYear()}`;
    }

    return `${getDateString(this.startDate)}-${
      this.endDate instanceof Date ? getDateString(this.endDate) : this.endDate
    }`;
  }
}

export interface IWorkExp {
  header: string;
  title: string;
  startDate: Date;
  endDate?: Date;
  descriptions: string[];
}

const InfoCard: React.FC<IWorkExp> = ({ header, title, startDate, endDate, descriptions }) => {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  return (
    <>
      <Card className={classes.card} elevation={2}>
        <CardHeader
          title={
            <>
              <b>{title}</b>
              <br />
              <Typography variant="h6">{header}</Typography>
            </>
          }
          subheader={new Duration(startDate, endDate).asText()}
          action={
            descriptions.length > 0 ? (
              <IconButton
                className={clsx(classes.expand, {
                  [classes.expandOpen]: expanded,
                })}
                onClick={handleExpandClick}
                aria-expanded={expanded}
                aria-label="show more"
              >
                <ExpandMoreIcon />
              </IconButton>
            ) : null
          }
          className={classes.cardHeader}
        />
        <Collapse in={expanded} timeout="auto" unmountOnExit>
          <CardContent className={classes.cardHeader}>
            <Paper elevation={0} className={classes.paper}>
              <List dense={true} className={classes.list}>
                {descriptions.map((description) => {
                  return (
                    <ListItem className={classes.list}>
                      <ListItemIcon>
                        <ArrowRightIcon />
                      </ListItemIcon>
                      <ListItemText primary={<Typography variant="body1">{description}</Typography>} />
                    </ListItem>
                  );
                })}
              </List>
            </Paper>
          </CardContent>
        </Collapse>
      </Card>
    </>
  );
};

export default InfoCard;
